import { useFactoryContract, useGuardContract, useDidContract } from '../../hooks/useContract'
import { useSingleCallResult } from '../multicall/hooks'
import { Contract } from '@ethersproject/contracts'
import { client } from '../../apollo/client'
import { ALL_LIST } from '../../apollo/queries'
import { useEffect, useState, useCallback } from 'react'

export function useGetGuardAddress(): string | undefined {
    const factoryContract = useFactoryContract()
    const res = useSingleCallResult(factoryContract, 'guard')
    if (res.result && !res.loading) {
        return res.result[0]
    }
    return undefined
}

export function useGetDidAddress(guardAddress: string | undefined): string | undefined {

    const guardContract = useGuardContract(guardAddress)
    const res = useSingleCallResult(guardContract, 'did')
    console.log('res', res)
    if (res.result && !res.loading) {
        return res.result[0]
    }
    return undefined
}

export function useNftBalance(didAddress: string | undefined, account: string | null | undefined): number | undefined {
    const didContract = useDidContract(didAddress)
    const res = useSingleCallResult(didContract, 'balanceOf', [account ?? ""])
    if (res.result && !res.loading) {
        return parseInt(res.result[0])
    }
    return undefined
}


export function useApplyLength(didContract: Contract | null): number {
    const res = useSingleCallResult(didContract, 'applyLength')
    if (res.result && !res.loading) {
        return parseInt(res.result[0])
    }
    return 0
}

async function getAllData() {
    try {
        let allFound = false
        let skipCount = 0
        let data: any = []
        while (!allFound) {
            let result = await client.query({
                query: ALL_LIST,
                variables: {
                    skip: skipCount,
                },
                fetchPolicy: 'cache-first',
            })
            data = data.concat(result?.data?.dids)
            if (result?.data?.dids?.length < 500 || data.length > 500) {
                allFound = true
            }
            skipCount = skipCount += 500
        }
        return data
    } catch (e) {
        console.log(e)
    }
}

export function useAllData() {

    const [data, setData] = useState([]);

    const fetchApiCallback = useCallback(async () => {
        let result = await getAllData()
        setData(result)
    }, [])

    useEffect(() => {
        fetchApiCallback()
    }, [fetchApiCallback])

    return data || []
}
